function _capstone_quant_filtered(capstone_quant){return(
capstone_quant.filter(x => x['How often do shoppers purchase groceries online?'] != "I currently do not buy groceries online" && x['How often do shoppers purchase groceries online?'] != "Less than once a month" && (x['What is your current age range?'] == "18 to 24 years old" || x['What is your current age range?'] == "25 to 35 years old") && x.Filter_out != 1 && x["Do you live in the United States?"] == "Yes" && x["What online grocery platforms do you use? (Check all that apply)"].includes("NextDoor") != true  && x["What online grocery platforms do you use? (Check all that apply)"].includes("Freshcart") != true)
)}

function _relationship_array(capstone_quant_filtered)
{
  let keys = Object.keys(capstone_quant_filtered[0])
  console.log(keys)
  let output_array = [];
  for (let i = 0; i<capstone_quant_filtered.length; i++){
    for (let j = 0; j<keys.length-1; j++) {
      if (j!= 0 && j != 3 && j!= 5 && j != 17 && j != 14 && j <18) {
        let number_of_x = capstone_quant_filtered.filter(x => x[keys[j]] == capstone_quant_filtered[i][keys[j]]).length;
        for (let k = 0; k<keys.length-1; k++) {
          if (k != 0 && k != 3 && k!= 5 && k != 14 && k != 17 && k <18) {
            let number_of_y = capstone_quant_filtered.filter(x => x[keys[k]] == capstone_quant_filtered[i][keys[k]]).length;
            let index = output_array.findIndex(x => x.val_1 == capstone_quant_filtered[i][keys[j]] && x.field_1 == keys[j] && x.field_2 == keys[k] && x.val_2 == capstone_quant_filtered[i][keys[k]]);
            if (index > -1) {
              output_array[index].count += 1;
            } else {
              // let full_x = "Question " + j + ": ";
              // let full_y = "Question " + k + ": ";
              // if (typeof capstone_quant_filtered[i][keys[j]] === 'number') {
              //   full_x += capstone_quant_filtered[i][keys[j]];
              // } else {
              //   full_x = 
              // }
              // if (typeof capstone_quant_filtered[i][keys[k]] === 'number') {
              //   full_y += capstone_quant_filtered[i][keys[k]];
              // }
              output_array.push({
                val_1: capstone_quant_filtered[i][keys[j]],
                field_1: keys[j],
                val_2: capstone_quant_filtered[i][keys[k]],
                field_2: keys[k],
                full_x: "Question " + j + ": " + capstone_quant_filtered[i][keys[j]],
                full_y: "Question " + k + ": " + capstone_quant_filtered[i][keys[k]],
                count: 1,
                total_num_1: number_of_x,
                total_num_2: number_of_y
              });
            }
          }
        }
      }
    }
  }
  return output_array;
}


function _larger_object(vl,relationship_array,Jarka_sans){return(
vl.markCircle()
  .data(relationship_array)
  .title({text: 'Quantitative Study Data Correlations', font: Jarka_sans, fontStyle: "Regular", fontSize: 20})
  .transform(
    vl.calculate('datum.count/datum.total_num_1').as('matrix_val_1'),
    vl.calculate('datum.count/datum.total_num_2').as('matrix_val_2'),
    vl.calculate('datum.matrix_val_1>.7').as('high')
  )
  .width(800)
  .height(800)
  .encode(
    vl.x().fieldO('full_x').axis({labelLimit: 1000, labelAngle: 60}).title(null),
    vl.y().fieldO('full_y').axis({font: Jarka_sans, fontSize: 20, labelLimit: 1000}).title(null),
    vl.color().fieldN('high').scale({range: ['#E19B26', '#4A6846']}).title("high correlation"),
    vl.opacity().fieldQ('matrix_val_1').title("relationship percentage"),
    vl.size().fieldQ('count').scale({domain: [0, 80]}).title("Number of responses"),
    vl.tooltip([{'title': "first question", 'field':'field_1'}, {'title': "answer 1", 'field':'val_1'}, {'title': "second question", 'field':'field_2'}, {'title': "second answer", 'field':'val_2'},  {'title': "Number that answered both", 'field':'count'}, {'title': "number of responses for first answer", 'field':'total_num_1'},{'title': "number of responses for second answer", 'field':'total_num_2'}, {'title': "percentage compared to first response", 'field':'matrix_val_1'}, {'title': "percentage compared to second response", 'field':'matrix_val_2'}])
  )
.render()
)}

