function _value_array(capstone_quant_filtered)
{
  let filter_dup = capstone_quant_filtered;
  let final_array = []
  for (let i = 0; i<filter_dup.length; i++) {
    let filtered_object = filter_dup[i];
    let string_val = filter_dup[i]['On product pages, what information do you look at when online grocery shopping? (Select all that apply)']
    let group;
    if (filter_dup[i]['When buying groceries who do you generally buy for?'] == 'I buy predominantly for myself and other people') {
    group = 'group';
    } else {
      group = 'solo';
    }
    let product_val = string_val.split(", ");
    let product_data_array = []
    for (let j = 0; j<product_val.length; j++){
      // filtered_object['product_val_obj'] = product_val[j];
      // filtered_object["buying for other people"] = group;
      product_data_array.push(product_val[j]);
    }
    filtered_object['product_val_obj'] = product_data_array;
    filtered_object['buying for other people'] = group;
    final_array.push(filtered_object)
  }
  return final_array;
}


