function _primary_object(capstone_quant_filtered)
{
  const object_array = ['Cultural upbringing and family traditions', 'Social media & food trends', 'Environmental and sustainability considerations', 'Information and news from healthcare/nutrition experts', 'Religious beliefs or practices', 'Personal Health Conditions'];

  let primary_object = [];
  for (let i = 0; i<capstone_quant_filtered.length; i++) {
    let other = capstone_quant_filtered[i];
    if (object_array.includes(capstone_quant_filtered[i]['What primarily shapes your current dietary habits?']) == false) {
      other['What primarily shapes your current dietary habits?'] = 'other';
    }
    primary_object.push(other);
  }

  return primary_object;
}


